import React, { Component } from 'react';
import { HashRouter, Route, Switch, Redirect } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import * as Loadable from './components/Loadable/Loadable';
import { LiliProvider } from './context/LiliContext';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userInfo: null
    };
  }

  render() {
    const { userInfo } = this.state;
    return (
      <HashRouter>
        <LiliProvider>
          <div className="app">
            <Loadable.Nav userInfo={userInfo} />
            <div className="app-content">
              <Switch>
                <Route exact path="/">
                  <Redirect to="/bai-hat" />
                </Route>
                <Route exact path="/bai-hat" component={Loadable.BaiHat} />
                <Route exact path="/bai-hat/:id" component={Loadable.BaiHat} />
                <Route exact path="/about" component={Loadable.About} />
                <Route path="" component={Loadable.NotFound} />
              </Switch>
            </div>
          </div>
        </LiliProvider>
        <ToastContainer />
      </HashRouter>
    );
  }
}

export default App;
